:root {
    /*Whitespace*/
    --whitespace-small: 12px;
    --whitespace-medium: 24px;
    --whitespace-large: 48px;
    --whitespace-xlarge: 128px;
    /*Font size's*/
    --fontsize-xsmall: 12px;
    --fontsize-small: 14px;
    --fontsize-medium: 24px;
    --fontsize-large: 48px;
    --fontsize-xlarge: 96px;
    /*Colours*/
    --whitespace-color: #edebe9;
    --Background-color: white;
}

body {
    width: 100%;
    font-family: Verdana, sans-serif;
    background-color: var(--Background-color);
}

h1 {
    color: var(--Background-color);
    line-height: 1.5;
}

img {
    vertical-align: middle;
    width: 150%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transform: translateX(0);
    opacity: 1;
}

.fade {
    transform: translateX(0);
    opacity: 1.0;
}


/* Header container */

.header-container {
    display: flex;
    align-items: flex-end;
    justify-content: right;
    padding: 5%;
    height: 800px;
    background-image: url("./components/Images/Banner.JPG");
    background-size: cover;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.header-content {
    width: 50%;
    font-size: var(--fontsize-large);
}

.fb-embed {
    display: flex;
    justify-content: center;
    padding: 2%;
    margin-left: 20%;
    margin-right: 20%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.fb-embed>iframe {
    width: 500px;
    height: 500px;
    border: none;
    overflow: hidden;
}

.white-space {
    height: var(--whitespace-xlarge);
    margin-bottom: 0.02%;
    margin-top: 0.2%;
    margin-left: 20%;
    margin-right: 20%;
}

.content-column {
    display: flex;
    align-items: center;
    margin-left: 20%;
    margin-right: 20%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.content-column-text {
    width: 50%;
    overflow: auto;
    padding: 8%;
    line-height: 1.4;
}

.content-column-images-right {
    overflow: visible;
    width: 50%;
    padding: 2%;
}

.content-column-images-left {
    overflow: visible;
    width: 50%;
    padding: 2%;
    direction: rtl;
}

@media only screen and (max-width: 970px) {
    h1 {
        color: var(--Background-color);
        line-height: 1.5;
        font-size: 75%;
    }
    .fb-embed {
        margin-left: 15%;
        margin-right: 15%;
    }
    .white-space {
        margin-left: 15%;
        margin-right: 15%;
    }
    .content-column {
        margin-left: 15%;
        margin-right: 15%;
    }
    .footer {
        display: flex;
        justify-content: center;
        border: 1px black solid;
    }
    .content-column-text {
        font-size: var(--fontsize-small);
    }
}

@media only screen and (max-width: 680px) {
    /* Header container */
    .header-container {
        height: 300px;
    }
    .header-content {
        font-size: var(--fontsize-large);
    }
    .content-column-text {
        font-size: var(--fontsize-xsmall);
    }
}

@media only screen and (max-width: 540px) {
    /* Header container */
    .header-container {
        height: 300px;
    }
    .header-content {
        font-size: var(--fontsize-medium);
    }
    img {
        width: 90%;
    }
    .fb-embed {
        margin-left: 5%;
        margin-right: 5%;
    }
    .white-space {
        height: var(--whitespace-large);
        margin-left: 5%;
        margin-right: 5%;
    }
    .content-column {
        display: flex;
        flex-direction: column;
        align-items: unset;
        margin-left: 5%;
        margin-right: 5%;
    }
    .content-column-text {
        width: 92%;
        margin-top: 5%;
        padding: 4%;
        font-size: var(--fontsize-xsmall);
        text-align: justify;
        line-height: 1.5;
    }
    .content-column-images-right {
        width: 100%;
        padding: 5%;
    }
    .content-column-images-left {
        width: 100%;
        padding: 5%;
        direction: ltr;
    }
}

@media only screen and (max-width: 340px) {
    /* Header container */
    .header-container {
        height: 300px;
    }
    .header-content {
        font-size: var(--fontsize-medium);
    }
    img {
        width: 90%;
    }
    .fb-embed {
        margin-left: 5%;
        margin-right: 5%;
    }
    .white-space {
        height: var(--whitespace-large);
    }
}